/* ========================== 
/////// BODY STYLING ///////*/

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
}

body {
  font-size: 16px;
  box-sizing: inherit;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

/* Sidebar styles */
.sidebar {
  position: fixed;
  top: 0;
  right: 0;
  height: 100%;
  width: 250px; /* Adjust width as needed */
  background-color: white;
  overflow-x: hidden;
  transition: transform 0.3s ease;
  z-index: 3050;
  color: black;
  padding: 60px 10px;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* Menu icon */
.menu__icon {
  font-size: 24px;
  cursor: pointer;
}

/* Close icon */
.close__icon {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 24px;
  cursor: pointer;
  color: black;
}

#navbar__log {
  text-align: center;
}

.log__text,
.user__img {
  cursor: pointer;
}

/* Overlay for closing sidebar */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 900;
}

@media only screen and (max-width: 800px) {
  .sidebar {
    display: flex;
    width: 100%;
  }
  .navbar__login_desktop {
    display: none;
  }
  .navbar__search {
    z-index: 999 !important;
  }

  #navbar_mb {
    z-index: 3500;
    left: 22px;
    background: none;
    width: 86%;
  }
  .navbar__log {
    padding: 19px;
    border: 1px solid black;
    width: 100%;
  }

  .user__card {
    display: inline-flex !important;
    align-items: center !important;
  }

  .user__name {
    border-left: 2px solid black;
    padding-left: 10px;
  }

  .log__text {
    margin-left: 0rem !important;
    border-bottom: none !important;
  }

  .sidebar__button {
    font-size: 26px !important;
    padding: 19px;
  }


  .logout__btn {
     width: 110% !important;
  }
}

@media only screen and (max-width: 75em) {
  html {
    font-size: 56.25%;
  }
}

@media only screen and (max-width: 1330px) {
  html {
    overflow-x: hidden !important;
  }

  body {
    overflow-x: hidden !important;
  }
}

@media only screen and (max-width: 56.25em) {
  html {
    font-size: 50%;
  }
}

@media only screen and (min-width: 112.5em) {
  html {
    font-size: 75%;
  }
}

/* ===========================
======= GRID SYSTEM ======== */

.row {
  max-width: 114rem;
  margin: 0 auto;
}

.row:not(:last-child) {
  margin-bottom: 8rem;
}

@media only screen and (max-width: 800px) {
  .row:not(:last-child) {
    margin-bottom: 6rem;
  }
}

@media only screen and (max-width: 800px) {
  .row {
    max-width: 50rem;
    padding: 0 3rem;
  }
}

.row::after {
  content: "";
  display: table;
  clear: both;
}

.row [class^="col-"] {
  float: left;
}

.row [class^="col-"]:not(:last-child) {
  margin-right: 6rem;
}

@media only screen and (max-width: 800px) {
  .row [class^="col-"]:not(:last-child) {
    margin-right: 0;
    margin-bottom: 6rem;
  }
}

@media only screen and (max-width: 800px) {
  .row [class^="col-"] {
    width: 100% !important;
  }
}

.row .col-1-of-2 {
  width: calc((100% - 6rem) / 2);
}

.row .col-1-of-3 {
  width: calc((100% - 2 * 6rem) / 3);
}

.row .col-1-of-4 {
  width: calc((100% - 3 * 6rem) / 4);
}

