/* ============================ 
/////// RECENT STYLING ///////*/

.recent__cont {
  margin-top: 32rem;
  padding: 2rem;
  margin-bottom: 1rem;
}

@media only screen and (max-width: 900px) {
  .recent__prod {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 20px;
    width: 200%;
    overflow-x: scroll !important;
  }

  .mobile__cont {
    border: 2px solid #d8dfe0;
    border-radius: 5px;
    cursor: pointer;
  }

  .recent__cont {
    margin-top: 32rem;
    padding: 2rem;
    margin-bottom: 1rem;
    overflow-x: scroll;
  }
  
  .cat__section::-webkit-scrollbar {
    display: none !important;
  }

  .cat__section::-webkit-scrollbar-track {
    display: none !important;
  }

  .cat__section::-webkit-scrollbar-button {
    display: none !important;
  }
  .recent__prod::-webkit-scrollbar {
    display: none !important;
  }

  .recent__prod::-webkit-scrollbar-track {
    display: none !important;
  }

  .recent__prod::-webkit-scrollbar-button {
    display: none !important;
  }
  .recent__cont::-webkit-scrollbar {
    display: none !important;
  }

  .recent__cont::-webkit-scrollbar-track {
    display: none !important;
  }

  .recent__cont::-webkit-scrollbar-button {
    display: none !important;
  }
}

