.cars__text {
  transform: translate(2rem, 1.5rem);
}

.car__cont {
  margin-top: -1rem;
}

.recent__text {
  font-size: 2.5rem;
  margin-bottom: 2rem;
}

.mobph__text {
  transform: translate(2rem, 31rem);
}

.mobile__img {
  width: 100%;
  height: 20rem;
  background-size: cover;
  background-position: center;
  object-fit: cover;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
}

.recent__prod {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
}

.mobile__cont {
  border: 2px solid #d8dfe0;
  border-radius: 5px;
  cursor: pointer;
}

.mobile__dil {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.mobile__box {
  padding: 1rem 1.5rem;
}

.mobile__name {
  margin-bottom: 2rem;
  color: #002f34;
  font-weight: 600;
  font-size: 1.6rem;
}

.mobile__price {
  color: #002f34;
  font-weight: 750;
}

.mobile__loc {
  color: #747474;
  font-weight: 600;
}

.mobile__day {
  color: #747474;
  font-weight: 600;
}

.home__inputs {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 700px;
  position: fixed;
  top: 8.2rem;
  right: 27rem;
  z-index: 1100;
}

.home__input {
  outline: none;
  border: 1px solid #d8dfe0;
  border-radius: 5px;
  padding: 1rem 2rem;
  font-size: 1.8rem;
  color: #002f34;
  width: 100%;
}

.home-mag__icon {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  background-color: #002f34;
  padding: 1.2rem;
  color: #fff;
  font-size: 2rem;
  cursor: pointer;
  transform: translateX(-0.3rem);
}

@media only screen and (max-width: 1330px) {
  .home__inputs {
